.Joke {
  display: flex;
  border-bottom: 2px solid #eeeeee;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  padding: 0.75rem;
}

.Joke-buttons {
  display: flex;
  margin-right: 1rem;
  align-items: center;
  justify-content: center;
  width: 15%;
}

.Joke-text {
  width: 75%;
  font-size: 1.2rem;
}

.Joke-votes {
  border-radius: 50%;
  text-align: center;
  border: 3px solid red;
  box-shadow: 0 10px 38px rgba(0, 0, 0, 0.2), 0 10px 12px rgba(0, 0, 0, 0.1);
  padding: 10px;
  margin: 5px;
}

.fa-arrow-up,
.fa-arrow-down {
  cursor: pointer;
  transition: all 0.3s;
}

.Joke-smiley {
  font-size: 1.5rem;
  margin-left: auto;
  border-radius: 50%;
  box-shadow: 0 10px 38px rgba(0, 0, 0, 0.2), 0 10px 12px rgba(0, 0, 0, 0.1);
}
